.App{
    display: flex;
    flex-direction: column;
    background: linear-gradient(180deg, rgba(250,235,215,1) 0%, rgba(255,160,34,1) 100%, rgba(17,36,36,1) 100%);
    width: fit-content;
}

.header{
    position: fixed;
}

.project-header-text{
    margin-top: 1vh;
    margin-bottom: 1vh;
}

.css-1elwnq4-MuiPaper-root-MuiAccordion-root{
    background-color: rgb(256,256,256,0.2) !important;
}

::ng-deep .css-67l5gl{
    background-color: rgb(256,256,256,0.2) !important;
}

.company-name{
    font-size: 1.5em;
}

.toggle-icon{
    height: 5vh;
    margin-top: 1vh;
    margin-left: 1vh;
    border-radius: 5px;
    padding: 0.5vh;
    cursor: pointer;
}

.sun{
    border: 2px solid black;
    filter: invert(1);
}

.moon{
    border: 2px solid black;
}

.night-mode{
    background: linear-gradient(180deg, rgba(19,117,117,1) 0%, rgba(47,79,79,1) 47%, rgba(17,36,36,1) 100%);
    color: #fff;

    .inst-image{
        filter: invert(1);
        mix-blend-mode: screen;
    }

    .contact-image-pic{
        filter: invert(1);
        mix-blend-mode: screen;
    }
}

.about-me{
    display: flex;
    /* background-color: antiquewhite; */
}

.night-switch{
    position: absolute;
}

.about-me-text{
    padding: 5vw;
    height: 50vh;
    width: 60%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    
}

.about-me-text-header{
    font-size: 4em;
    font-weight: 800;
}

.atlas-image{
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 5vw;
}

.contact-image{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40%;
    height: 30vh;
}

.atlas-image-pic{
    width: 230px;
    object-fit: cover;
    border-radius: 10%;
    border: 15px solid #fff;
}

.work-experience{
    /* background-color: antiquewhite; */
    display: flex;
}

.work-experience-image{
    width: 40%;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50vh;
}

.work-experience-timeline{
    padding: 5vw;
    display: flex;
    flex-direction: column;
}

.inst-image{
    transform: scale(0.3);
    mix-blend-mode: multiply;
}

.contact-image-pic{
    width: 100%;
    mix-blend-mode: multiply;
}

.contact-text{
    padding: 5vw;
    height: 30vh;
    width: 60%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

@media only screen and (max-width: 600px) {
    .about-me {
      flex-direction: column-reverse;
      width: 100vw;
    }

    .about-me-text{
        max-width: 450px;
        width: 90vw;
    }

    .about-me-text-header{
        max-width: 400px;
    }

    .work-experience{
        flex-direction: column;
        width: 100vw;
    }

    .work-experience-timeline{
        max-width: 400px;
    }

    .atlas-image{
        width: 100vw;
        margin-top: 20vw;
    }

    .work-experience-image{
        width: -webkit-fill-available;
    }

    .contact-image{
        width: 90vw;
    }
  }